.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.line {
  width: 90.4%;
  height: .8vw;
  margin: 0 auto;
  background: #000;
}
.cs-depth3 ul {
  margin: 4.8vw 0;
}
.li-coupon {
  background: #f3f4f7;
  .box-coupon {
    overflow: hidden;
    padding: 7.47vw 7.47vw 0;
    .cnts {
      float: left;
      width: 66.4vw;
      height: 37.87vw;
      position: relative;
      padding: 5.06vw 5.6vw;
      box-sizing: border-box;
      .brand-name {
        font-size: 3.466vw;
        font-weight: 700;
      }
      h4 {
        font-size: 3.733vw;
        font-weight: 700;
        line-height: 5.066vw;
        small {
          font-size: 3.2vw;
          color: #898989;
        }
      }
      .dscnt {
        position: absolute;
        font-size: 5.6vw;
        font-weight: 700;
        bottom: 3.733vw;
        span {
          font-weight: 700;
        }
      }
      .date {
        position: absolute;
        font-size: 2.93vw;
        font-weight: 700;
        color: #aaa;
        right: 5.6vw;
        bottom: 4vw;
        span {
          font-weight: 700;
        }
      }
    }
    .list01 {
      background: #ff87a3 url('/_ui/img/m/temp/coupon-list01.jpg') no-repeat center;
      background-size: cover;
      color: #fff;
    }
    .list-nmtype {
      background: #fff;
      color: #1f1f1f;
    }
    .btn-dw {
      float: left;
      position: relative;
      display: block;
      width: 18.66vw;
      height: 37.87vw;
      background: #f23d4e url('/_ui/img/m/icn/icn-arrow-dw.png') no-repeat center;
      background-size: 30%;
      cursor: pointer;
      span {
        position: absolute;
        display: inline-block;
        width: 100%;
        bottom: 5.33vw;
        font-size: 2.93vw;
        color: #fff;
        text-align: center;
      }
    }
    .cp-dscnt {
      float: left;
      position: relative;
      display: block;
      width: 18.66vw;
      height: 37.87vw;
      background: #f23d4e;
      cursor: pointer;
      span {
        position: absolute;
        display: inline-block;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
        font-size: 4.533vw;
        color: #fff;
        text-align: center;
      }
    }
    .li-info {
      clear: both;
      overflow: hidden;
      padding-top: 2.4vw;
      padding-bottom: 7.47vw;
      border-bottom: 2px solid #e1e1e1;
      dd,
      dt {
        display: inline-block;
        padding-top: 1.6vw;
        font-size: 3.2vw;
        color: #898989;
      }
      dt {
        width: 18%;
        vertical-align: top;
      }
      dd {
        width: 79%;
        strong {
          color: #1f1f1f;
          font-weight: normal;
        }
        a {
          color: #1f1f1f;
          text-decoration: underline;
        }
      }
    }
  }
  .list-non {
    padding: 31.33vw 0;
    font-size: 4.26vw;
    color: #aaa;
    text-align: center;
  }
  .last {
    .li-info {
      border: 0;
    }
  }
}
.info-use {
  padding: 9.33vw 4.8vw;
  h4 {
    margin-bottom: 5.06vw;
    font-size: 3.733vw;
    font-weight: 700;
    color: #1f1f1f;
  }
  li {
    padding-bottom: 2.66vw;
    font-size: 3.46vw;
    color: #aaa;
    line-height: 4.8vw;
    span {
      display: inline-block;
      margin-left: 1.86vw;
    }
  }
}
.kolon-header {
  //background: #f3f4f7;
}
.offline-coupon-shop {
  padding-top: 17.6vw;
  background-color: #f3f4f7;
  .list-dot-style {
    margin-bottom: 6vw;
    padding: 0 6.5vw;
    li {
      font-size: 3.2vw;
      line-height: 4.5vw;
    }
  }
  .scroll-area {
    height: 80%;
    padding: 0 6.5vw;
    overflow-y: scroll;
    .select-shop {
      background-color: #fff;
      margin-bottom: 6vw;
      padding: 4vw 5vw;
      select,
      input[type="text"] {
        float: left;
        height: 10vw;
        margin-bottom: 3vw;
        border-bottom: .266vw #f1f1f1 solid;
        font-size: 3.5vw;
      }
      select {
        width: 35vw;
        padding-right: 2.7vw;
        background: url(/_ui/img/m/icn/icn-arrow02.png) no-repeat 99% 50%;
        background-size: 2.2vw;
        &:first-child {
          margin-right: 4vw;
        }
      }
      input {
        &[type="text"] {
          width: 50.5vw;
          margin-right: 4vw;
        }
      }
      .btn {
        color: #1f1f1f;
        font-size: 3.5vw;
        font-weight: 700;
        width: 20.5vw;
        height: 9vw;
        margin-top: 1vw;
        margin-bottom: 3vw;
        border: .266vw #f1f1f1 solid;
      }
    }
    .shop-list {
      padding: 2vw;
      li {
        font-size: 3.5vw;
        position: relative;
        padding-bottom: 6vw;
        &:not(:first-child)::before {
          content: '';
          position: absolute;
          top: -6vw;
          width: 100%;
          height: .33vw;
          margin: 3vw 0;
          background: #e1e1e1;
        }
      }
    }
    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
      background-color: #f7f7f7;
    }
    &::-webkit-scrollbar-thumb {
      position: relative;
      background: #666;
    }
  }
}

@media (min-width: 750px) {
  .scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide a {
        height: 100px;
        line-height: 100px;
        font-size: 30px;
      }
    }
    .swiper-wrapper + .icn-navi {
      width: 35px;
      height: 115px;
      right: -36px;
    }
    .swiper-wrapper + .icn-navi.left {
      left: -36px;
    }
  }
  .line {
    height: 6px;
  }
  .cs-depth3 ul {
    margin: 36px 0;
  }
  .li-coupon {
    .box-coupon {
      padding: 56px 56px 0;
      .cnts {
        width: 498px;
        height: 284px;
        padding: 38px 42px;
        .brand-name {
          font-size: 26px;
        }
        h4 {
          font-size: 28px;
          line-height: 38px;
          small {
            font-size: 24px;
          }
        }
        .dscnt {
          font-size: 42px;
          bottom: 28px;
        }
        .date {
          font-size: 22px;
          bottom: 30px;
        }
      }
      .btn-dw {
        width: 140px;
        height: 284px;
        span {
          bottom: 40px;
          font-size: 22px;
        }
      }
      .cp-dscnt {
        width: 140px;
        height: 284px;
        span {
          font-size: 34px;
        }
      }
      .li-info {
        padding-top: 18px;
        padding-bottom: 56px;
        dd,
        dt {
          padding-top: 12px;
          font-size: 24px;
        }
      }
    }
    .list-non {
      padding: 235px 0;
      font-size: 32px;
    }
  }
  .info-use {
    padding: 70px 36px;
    h4 {
      margin-bottom: 38px;
      font-size: 28px;
    }
    li {
      padding-bottom: 20px;
      font-size: 26px;
      line-height: 36px;
      span {
        margin-left: 14px;
      }
    }
  }
  .offline-coupon-shop {
    padding-top: 132px;
    .list-dot-style {
      margin-bottom: 45px;
      padding: 0 49px;
      li {
        font-size: 24px;
        line-height: 34px;
      }
    }
    .scroll-area {
      padding: 0 49px;
      .select-shop {
        margin-bottom: 45px;
        padding: 30px 37px;
        select,
        input[type="text"] {
          height: 75px;
          margin-bottom: 23px;
          border-width: 2px;
          font-size: 26px;
        }
        select {
          width: 262px;
          padding-right: 20px;
          background-size: 17px;
          &:first-child {
            margin-right: 30px;
          }
        }
        input {
          &[type="text"] {
            width: 379px;
            margin-right: 30px;
          }
        }
        .btn {
          font-size: 26px;
          width: 154px;
          height: 67px;
          margin-top: 8px;
          margin-bottom: 23px;
          border-width: 2px;
        }
      }
      .shop-list {
        padding: 15px;
        li {
          font-size: 26px;
          padding-bottom: 45px;
          &:not(:first-child)::before {
            top: -45px;
            height: 3px;
            margin: 23px 0;
          }
        }
      }
    }
  }
}
